body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
    color: white
}

a:hover {
    color: red;
    text-decoration: none;
}
i {
    font-size: 34px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.modal {
    z-index: 10500;
}

textarea {
    width: 100%;
}
.api-cards {
    width: 100%;
    height: auto;
    border-radius: 8px;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.4);
    top: 0;
    text-align: center;
    transition: all 0.25s;
    margin-bottom: 2em;
}

.searched-trail {
    width: 100%;
}

.card-image {
    height: 400px;
}
body {
    background-color: #fcfcfc;
}

.carousel-item img {
    width: 75%;
    height: 600px;
    margin: 0 auto;
    opacity: 0.09px;

}

h3 {
    margin-bottom: 5em;
    font-family: 'Playfair Display', serif;
    font-size: 45px;
    font-weight: 800;
    color: whitesmoke;
    text-shadow: 1px;

}

.overlay {
    width: 100%;
    height: 600px;
    position: absolute;
    z-index: 15;
    background-color: rgba(0, 0, 0, 0.5);
}

.black {
    color: black;
}
body {
    background-color: rgb(225, 230, 227);
}

.profile-card {
    margin: auto;
    width: 400px;
    height: auto;
    margin-bottom: 1em;

}

.profile {
    padding-top: 2em;
}

.saved-card {
    width: 100%;
    height: auto;
}

.list-trail {
    font-size: 20px
}
.card-img {
    height: 400px;
}
